body {
    font-family: 'Cabin', sans-serif;
    margin: 0;
    box-sizing: border-box;
}

header,
footer {
    height: 100vh;
    text-transform: uppercase;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
}
header dd {
    margin: 1rem;
}
header nav {
    margin: 2rem;
}
header nav ul {
    list-style: none;
    display: inline-flex;
    padding: 0;
}
header nav ul li {
    margin: 0 1rem;
}

section {
    height: 100vh;
    box-sizing: border-box;
}

#map {
    height: 100%;
}

#synopsis {
    padding: 2rem;
    background-color: lightgray;
}

footer {
    background-color: grey;
    color: white;
}

@media (min-width: 860px) {
    #synopsis {
        padding: 4rem;
    }
}
